.ton {
  position: relative;
  width: 130px;
  height: 35px;
  font-size: 11px;
  letter-spacing: 1px;
  font-weight: 500;
  color: #000;
  background-color: rgb(20, 216, 216);
  border: none;
  border-radius: 5px;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease 0s;
  cursor: pointer;
  outline: none;
  margin-bottom: -100px;
  margin-left: -130px;
}

.ton:hover {
  background-color: #2ee59d;
  border: none;
  box-shadow: 0px 15px 20px rgba(46, 229, 157, 0.4);
  color: rgb(0, 0, 0);
  transform: translateY(-7px);
}
