.gallery {
    column-count: 3;
    -webkit-column-width: 33%;
    -moz-column-width: 33%;
    columns: 400px;
    padding: 0 25px;
}

.gallery .pics {
    transition: all 350ms ease;
    cursor: pointer;
    margin-bottom: 10px;
}

.gallery .pics:hover {
    filter: opacity(.8);
}

