.foto-slider img {
  width: 380px;
  height: 420px;
  object-fit: cover;
  border-radius: 10px;
  box-shadow: 1px 3px 10px rgb(123, 123, 123);
  /* padding: 60px; */
  margin: 40px;
}
.foto-slider img:hover {
  transition: 0.2s all ease-out;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: absolute;
  opacity: 0.5;
  box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
  transform: scale(1.1, 1.1);
}

.text-slider {
  position: absolute;
  color: blue;
  padding-top: 3em;
  padding-left: 2em;
  margin-top: 0;
  color: white;
  text-shadow: 2px 2px 4px rgb(20, 216, 216);
}

